<template>

  <el-container class="page-container scrollAllY">
    <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
      <!--工具栏-->
      <el-form class="queryForm" label-width="100px"  ref="queryForm" :inline="true" :model="filters" :size="size" label-position="right">
        <el-collapse class="elCollapseItemNoBoeder elCollapseDict" >
          <el-row>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span>上传人</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item  class="dictInputQueryWidth textAlign">
                <el-input   maxlength="32" type="text"  v-model="filters.uploadMan" placeholder="上传人" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span>附件分类</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth textAlign">
                <el-select  class="widthAll"  v-model="filters.fileType" placeholder="请选择" clearable>
                  <el-option class="dictQueryOptionPadding" v-for="item in queryFormDefaultOptions"
                             :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span>文件名称</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item  class="dictInputQueryWidth textAlign">
                <el-input    v-model="filters.fileName"  maxlength="100" type="text" placeholder="文件名称" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-collapse-item class="elCollapseItemNoBoeder elCollapseItemMarginBottom">
            <el-row>
              <el-col :span="2">
                <el-form-item class="dictInputQueryLabelWidth">
                  <span>文件备注</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item class="dictInputQueryWidth textAlign">
                  <el-input   v-model="filters.remark"  maxlength="100" type="text" placeholder="文件备注" clearable></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="2">
                <el-form-item class="dictInputQueryLabelWidth">
                  <span>上传时间</span>
                </el-form-item>
              </el-col>
              <el-col :span="14">
                <el-form-item  class="dictInputQueryWidth textAlign widthJiuBa ">
                  <el-date-picker
                    v-model="timeAll"
                    class="widthAll"
                    type="datetimerange"
                    :picker-options="pickerOptions"
                    value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm:ss"
                    range-separator="至"
                    start-placeholder="上传开始时间"
                    end-placeholder="上传结束时间"
                    align="right">
                  </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>
          </el-collapse-item>
        </el-collapse>
        <el-row class="elDeptFormButton dictQueryCenterButton">
          <el-col :span="24">
            <cb-button icon="fa fa-search" :label="$t('action.search')"   perms="sys:tsysattach:query"
                       type="primary" @click="findPages(null)"/>
          </el-col>
        </el-row>

      </el-form>
    </el-header>
    <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
      <!--表格内容栏-->
      <cb-table  permsDelete="sys:tsysattach:remove"
                :isOpen="isOpen"
                :data="pageResult" :columns="columns" @selectionChange="selectionChange"
                @toggleRowSelection="toggleRowSelection"
                @findPage="findPages"  @handleDeleteOther="handleDelete" >
      </cb-table>

      <!--<cb-table-dict permsEdit="sys:dict:edit" permsDelete="sys:dict:delete"
                     :data="pageResult" :columns="columns"
                     :isOpen="isOpen" @toggleRowSelection="toggleRowSelection"
                     @findPage="findPages" @selectionChange="selectionChange"> &lt;!&ndash;@handleEdit="handleEdit" @handleDelete="handleDelete"&ndash;&gt;
      </cb-table-dict>-->
    </el-main>
  </el-container>

</template>

<script>
  import CbTableDict from "@/views/Core/CbTableDict"
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import { format } from "@/utils/datetime"
  export default {
    components:{
      CbTableDict,
      CbTable,
      CbButton
    },
    data() {
      return {
        isOpen : true, // 是否开启点击表格行也选中
        queryFormDefaultOptions:[
          {value: '',label: '全部' },
          {value: '1',label: '字典' }
        ],
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        size: 'small',
        timeAll:[],
        filters: {
          uploadMan: "",
          fileType:"",
          fileName:"",
          remark:"",
          suploadTime:"",
          euploadTime:""

        },
        columns: [
          {prop:"createUserId", label:"上传人", minWidth:60},
          {prop:"createTime", label:"上传时间", minWidth:60}, //, formatter:this.dateFormat
          {prop:"ljUrl", label:"链接地址", minWidth:95},
          {prop:"pkid", label:"关联ID", minWidth:85},//如果未提交则为空
          {prop:"remark", label:"文件备注", minWidth:60},
          {prop:"fileType", label:"附件分类", minWidth:60},//
          {prop:"fileName", label:"文件名称", minWidth:60},//
          /*{prop:"fileUrl", label:"文件路径", minWidth:95},*/
          {prop:"fileState", label:"文件状态", minWidth:60},//0是未提交,1是已提交
        ],
        pageRequest: {
           totalCount: 188,//总共多少条数据
           PageSize: 10,//一页显示几条  limit
           totalPage: 63,//有多少页
           pageSizes:[10, 20, 30, 40],
          pageNum: 1,//当前页码  page
           pagerCount:5,//显示几个页数按钮
        },
        pageResult: {},
        selections: [] // 列表选中列
      }
    },
    methods: {
      toggleRowSelection(result) {
        this.selections=[];
        this.selections = result.list;
      },
      blurDateEnd:function(){
        if(this.filters.suploadTime){
          if (this.filters.euploadTime) {
            if( new Date(this.filters.suploadTime).getTime() > new Date(this.filters.euploadTime).getTime() ){
              this.$message({message: "结束时间必须比开始时间大", type: 'warning'});
              document.getElementById("euploadTime").style.color='red';
              document.getElementById("euploadTime").style.borderColor='red';
              return;
            }else{
              document.getElementById("euploadTime").style.color='#606266';
              document.getElementById("euploadTime").style.borderColor='#dcdfe6';
            }
          }else{
            document.getElementById("euploadTime").style.color='#606266';
            document.getElementById("euploadTime").style.borderColor='#dcdfe6';
          }
        }else{
          document.getElementById("euploadTime").style.color='#606266';
          document.getElementById("euploadTime").style.borderColor='#dcdfe6';
        }
      },
      // 获取分页数据
      findPages: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest
        }
        this.filters.page=this.pageRequest.pageNum;
        this.filters.limit=this.pageRequest.PageSize;
        if(this.timeAll){
          this.filters.suploadTime=this.timeAll[0];
          this.filters.euploadTime=this.timeAll[1];
        }
        this.$api.attach.findPage(this.filters).then(res => {
          this.pageResult=res.data;

        }).then(data!=null?data.callback:'');


      },
      // 批量删除
      handleDelete: function (data) {
        if(data.obj){
          let jj=[];
          jj.push(data.obj.attachId);
          this.delete(jj);
        }else{
          if(data.params){
            let jj=[];
            for(let k=0;k<data.params.length;k++){
              jj.push(data.params[k].attachId);
            }
            this.delete(jj);
          }
        }
      },
      delete:function(jj){
        this.$confirm("确认删除选中记录吗？", "提示", {
          type: "warning",
          closeOnClickModal:false
        }).then(() => {
          this.$api.attach.batchDelete(jj).then(res => {
            if(res.code!==0){//删除失败
              this.$message.error(res.msg);
              return;
            }else{
              this.$message({ message: "操作成功", type: "success" });
            }
            this.findPages(null);
          })
      }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //获取当前选中行
      selectionChange:function(params){
        this.selections=params.selections;
      },
      // 时间格式化
      dateFormat: function (row, column, cellValue, index){
        return format(row[column.property])
      },
      findDataOrigin:function () {
        this.$api.parameter.findParaType({paraType:'fileType'}).then(res => {
          if(res.data){
            this.queryFormDefaultOptions=[];
            for(let i=0;i<res.data.length;i++){
              let para={};
              para.value=res.data[i].parano;
              para.label=res.data[i].paraKey;
              this.queryFormDefaultOptions.push(para);
            }
          }
        });

      }
    },
    mounted() {
      this.findDataOrigin();
    }
  }
</script>

<style scoped>
</style>



