<script>
  import PopupTreeInput from "@/components/PopupTreeInput"
  import PopupTreeInputCheck from "@/components/PopupTreeInputCheck"
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import TableColumnFilterDialog from "@/views/Core/TableColumnFilterDialog"
  import { format } from "@/utils/datetime"
  import Cookies from "js-cookie";
  import {baseUrl} from "../../utils/global";

  export default {
    components:{
      PopupTreeInput,
      CbTable,
      PopupTreeInputCheck,
      CbButton,
      TableColumnFilterDialog
    },
    data() {
      var validateUserName=(rule, value, callback) => {
        if (value) {
          if( !/^[a-zA-Z][a-zA-Z0-9_]*$/.test(value)) {
            callback(new Error('必须是字母开头，字母数字下划线组成'));
          } else {
            callback();
          }
        } else {
          callback(new Error('登录名不能为空'));
        }
      };
      var validateZip=(rule, value, callback) => {
        if (value) {
          if ( !/^[1-9][0-9]{5}$/.test(value)) {
            callback(new Error('邮政编码不正确'));
          } else {
            callback();
          }
        }
        else {
          callback();
        }
      };
      var validateEmail= (rule, value, callback) => {
        if (value) {
          if ( !/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(value)) {
            callback(new Error('邮箱格式不正确'));
          } else {
            callback();
          }
        }
        else {
          callback();
        }
      };
      var validateUserCard = (rule, value, callback) => {
        if (value) {
          /*    0731-89913439*/
          if ( !/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(value)) {
            callback(new Error('身份证格式不正确'));
          } else {
            callback();
          }
        }
        else {
          callback();
        }
      };
      var validateMobile = (rule, value, callback) => {
        if (value) {
          if(isNaN(value)){
            callback(new Error('请输入数字'));
          }else{
            if ( !(/^1(3|4|5|7|8|6)\d{9}$/.test(value))) {
              callback(new Error('手机号码格式不正确'));
            }else {
              callback();
            }
          }
        }
        else {
          callback(new Error('手机号码不为空'));
        }
      };
      return {
        valueCSK:[],
        setdataRole:[],
        setUserName:[],
        setUserId:[],
        defaultCheckedKeys : [], // 机构树默认展开的节点
        defaultExpandedkeysForQueryOrgId : [], // 查询条件之所属机构默认展开的节点
        clearableForInput : true, // popupTreeInput是否显示删除图标
        isOpen : true, // 是否开启点击表格行也选中
        isShow : true, // 是否开启Switch 开关
        showUserName : true, // 是否开启点击表格行弹出框
        actionUrl:baseUrl+'/api/sys/user/upload',
        displayType:[{type:"1",name:"只查询本级机构用户"},{type:"2",name:"查询本级机构及下级机构用户"}],
        postValue : '', // 所属岗位的值
        value:'',
        attachId:'',
        imageUrl: '', // 头像上传成功后，用该值回显
        uploadHeaders : {},
        defaultExpandAllForSave : true, // 是否全部展开
        parentIdForSave : 0, // 该值用于新增或修改中机构树某项被选中
        parentNameForSave : '', // 该值用于新增或修改中机构树某项被选中
        parentNameForSaveTwo: '', // 该值用于新增或修改中副机构树某项被选中
        popupTreeDataForQuery:[],
        popupTreeDataTwo:[],
        popupTreeOrgIdList:[],
        popupTreePropsForQuery: {
          label: 'name',
          children: 'list'
        },
        selections: [],  // 列表选中列
        size: 'small',
        filterleftTree:'',
        menuData: [],
        activeNames: ["1"],//折叠面板初始化：初识显示哪个面板
        filters: {
          name: ''
        },
        columns: [],
        filterColumns: [],
        pageRequest: {},
        pageResult: {},
        sex: [],
        usertype: [],
        status: [],
        operation: false, // true:新增, false:修改
        dialogVisible: false, // 新增修改界面是否显示
        dialogVisibleRead: false, // 只读界面是否显示
        dialogVisibleRole: false, // 分配角色界面是否显示
        dialogRoleJur: false, // 新增修改界面是否显示
        editLoading: false,
        menuLoading: false,
        dataUser: {
          username: [{required: true, validator: validateUserName, trigger: 'blur' }],
          userRealname: [{required: true, message:"姓名不能为空", trigger: 'blur' }],
          mobile:[{required: true, validator: validateMobile, trigger: 'blur' }],
          userCard:[{ validator: validateUserCard, trigger: 'blur' }],
          email:[{ validator: validateEmail, trigger: 'blur' }],
          zip:[{ validator: validateZip, trigger: 'blur' }],
        },
        // 新增修改界面数据
        dataForm: {
          userId:'',
          roleName:'',
          showRoleName:[],
          username :'',
          userRealname :'',
          parentId:0,
          password :'',
          postIdList:[],
          orgId :'',
          orgIdList:[],
          sex :"0",
          userimg:'',
          userType :"0",
          status:"1",
          userTheme:'',
          userCard:'',
          nativePlace:'',
          email :'',
          zip :'',
          birthday:'',
          nation:'',
          mobile:'',
        },
        deptData: [],
        props: {
          label: 'name',
          children: 'zones'
        },
        count: 1,
        roles: [],
        filterText: '',
        dataleftTree: [],
        dataPostTree:[],
        dataRole:[],
        defaultProps:{
          children:'children',
          label:'label'
        },
        testTreeName:'',
        testTreeId:'',
      }
    },
    watch: {
      filterleftTree(val) {
        this.$refs.leftTree.filter(val);
      }
    },
    methods: {
      handleTreeSelectChangeForSave (data, node){
        this.parentIdForSave = data.id;
        this.parentNameForSave  = data.name
        this.dataForm.orgId = data.id; // 给新增或修改界面中javabean所属机构赋值
      },
      // 控制列表查询条件中所属机构输入框不可输入
      focusForQuery(event){
        event.target.setAttribute("readonly", "readonly")
      },
      //参数转换
      changeParams:function(){
        this.$api.parameter.findParaType({paraType:'userType'}).then(res => {
          if(res.data){
            this.usertype=[];
            for(let i=0;i<res.data.length;i++){
              let para={};
              para.type=res.data[i].parano;
              para.name=res.data[i].paraKey;
              this.usertype.push(para);
            }
          }
        });
        this.$api.parameter.findParaType({paraType:'status'}).then(res => {
          if(res.data){
            this.status=[];
            for(let i=0;i<res.data.length;i++){
              let para={};
              para.type=res.data[i].parano;
              para.name=res.data[i].paraKey;
              this.status.push(para);
            }
          }
        });
        this.$api.parameter.findParaType({paraType:'sex'}).then(res => {
          if(res.data){
            this.sex=[];
            for(let i=0;i<res.data.length;i++){
              let para={};
              para.type=res.data[i].parano;
              para.name=res.data[i].paraKey;
              this.sex.push(para);
            }
          }
        });
      },
      rowClick(){},
      //所属副机构
      getOrgIdList: function(data){
        this.dataForm.orgIdList=[];
        for(let i=0;i<data.length;i++){
          this.dataForm.orgIdList.push(data[i][data[i].length-1]);
        }
      },
      //只读
      ReadName(result){
        this.dialogVisibleRead = true;
        this.dataForm = Object.assign({}, result.row);

        if (this.operation == false){
          var data = {userId:this.dataForm.userId};
          this.$api.user.findView(data.userId).then((res) => {
            this.imageUrl=baseUrl+'/uploads/sys-user-img/'+res.data.userimg;
            this.dataForm.orgId=res.data.orgId;
            this.dataForm.userimg=res.data.userimg;
            this.dataForm.sex=res.data.sex;
            this.dataForm.status=res.data.status;
            this.dataForm.userType=res.data.userType;
            this.parentNameForSave = res.mainOrgName;
            this.dataForm.showRoleName=[];
            for(let i=0;i<res.data.roleNameList.length;i++){
              var obj = {
                key:i,
                label : res.data.roleNameList[i],
              };
              this.dataForm.showRoleName.push(obj);
            }
            if(res.data.postIdList.length==0){
              this.postValue='';
              this.dataForm.postIdList=this.postValue;
            }else{
              this.postValue = res.data.postIdList[0].split(",")[0];
            }

          });
        }
        this.findPage(null);
      },
      // switch开关
      switchChange(result){
        var val = result.e; // 0:关闭状态，1开启状态

        var userId = result.data.userId; // 角色ID
        var obj = {userId : userId, status : val};
        this.$api.user.updataStatus(obj).then((res) => {
          if (res.code == 0){
            if (val == 0){
              this.$message({ message: '禁用成功', type: 'warning' });
            } else {
              this.$message({ message: '启用成功', type: 'success' });
            }
          } else {
            if (val == 0){
              this.$message({ message: '禁用失败', type: 'warning' });
            } else {
              this.$message({ message: '启用失败', type: 'success' });
            }
          }
        });
      },
      //分配角色
      setRole:function(){
        if(this.selections.length<1){
          this.$message({message: "请先选择一条用户记录!", type: 'warning'});
          return;
        }
        else{
          let count=0;
          for(let i=0;i<this.selections.length;i++){
            if(this.selections[i].status==1){
              count++;
            }
          }
          if(count!=this.selections.length){
            this.$message({message: "不能给禁用的用户分配角色!", type: 'warning'});
          }
          else{
            this.valueCSK=[];
            this.dialogVisibleRole=true;
            this.setUserName=[];
            this.setUserId=[];
            //显示给哪些用户分配角色
            for(let i=0;i<this.selections.length;i++){
              var obj = {
                key:this.selections[i].userId,
                label : this.selections[i].userRealname,
              };
              this.setUserName.push(obj);
              if(i==this.selections.length-1) {
                this.setUserId += this.selections[i].userId;
              }else{
                this.setUserId+=this.selections[i].userId+',';
              }
            }
            //渲染出所有角色
            this.$api.role.queryAll().then((res) => {
              var arr=[];
              if (res.data.length > 0){
                for (let i=0; i<res.data.length; i++){
                  let role = res.data[i];
                  var obj = {
                    key:role.roleId,
                    label : role.roleName,
                    roleId : role.roleId,
                    id : role.roleId
                  };
                  arr.push(obj);
                }
              }
              this.dataRole = arr;
              this.setdataRole= arr;
            });
            //回选出所选的角色
            let params=[];
            this.valueCSK=[];
            for(let i=0;i<this.setUserId.split(",").length;i++){
              params.push(this.setUserId.split(",")[i]);
            }
            this.$api.user.toGrantRole(params).then((res) => {
              for(let i=0;i<res.data.length;i++){
                this.valueCSK.push(res.data[i].roleId);
              }
            });
          }
        }
      },
      //删除分配用户的标签
      handleClose(tag){
        if(this.setUserName.length==1){
          this.$message({message: "至少选择一个!", type: 'warning'});
        }else{
          this.setUserId=[];
          for(let i=0;i<this.setUserName.length;i++){
            if(this.setUserName[i].key==tag){
              this.setUserName.splice(i, 1);
            }
          }
          for(let i=0;i<this.setUserName.length;i++){
            if(i==this.setUserName.length-1) {
              this.setUserId += this.setUserName[i].key;
            }else{
              this.setUserId+=this.setUserName[i].key+',';
            }
          }

          //回选出所选的角色
          let params=[];
          this.valueCSK=[];
          for(let i=0;i<this.setUserId.split(",").length;i++){
            params.push(this.setUserId.split(",")[i]);
          }
          this.$api.user.toGrantRole(params).then((res) => {
            for(let i=0;i<res.data.length;i++){
              this.valueCSK.push(res.data[i].roleId);
            }
          });
        }
      },
      //保存分配角色
      saveUserRole:function(){
        this.$confirm('确认提交吗？', '提示', {}).then(() => {
          this.dialogVisibleRole = false;
          var roleIds='';
          for(let i=0;i<this.valueCSK.length;i++){
            if(i==this.valueCSK-1){
              roleIds+=this.valueCSK[i];
            }else{
              roleIds+=this.valueCSK[i]+',';
            }
          }
          let params={};
          params.roleIds=roleIds;
          params.userIds=this.setUserId;

          this.$api.user.grantRole(params).then((res) => {
            if(res.code == 0) {
              this.$message({ message: '操作成功', type: 'success' })
            } else {
              this.$message({message: '操作失败, ' + res.msg, type: 'error'})
            }
            this.findPage(null)
          })
        })

      },
      //当前树的节点被选中
      handleSelectMenuTree: function(data){
        this.dataForm={};
        let test={};
        Object.assign(test, data);
        this.dataForm=test;
        let jj={};
        jj.sidx = "update_time";
        jj.order = "desc";
        this.testTreeName=test.name;
        this.testTreeId=test.id;
        jj.pageNum=this.pageRequest.pageNum;
        jj.pageSize=this.pageRequest.PageSize;
        jj.mobile=this.filters.mobile;
        jj.status=this.filters.status;
        jj.username=this.filters.username;
        jj.userCard=this.filters.userCard;
        jj.displayType=this.filters.displayType;
        jj.sex=this.filters.sex;
        jj.orgId=this.testTreeId;
        this.$api.user.findPage(jj).then((res) => {
          for(let i=0;i<res.data.list.length;i++){
            res.data.list[i].userimg=baseUrl+'/uploads/sys-user-img/'+res.data.list[i].userimg
            if(res.data.list[i].status=='正常'){
              res.data.list[i].status='1'
            }else{
              res.data.list[i].status='0'
            }
          }
          this.pageResult = res.data;
          jj.orgId='';
        }).then(data!=null?data.callback:'')
      },
      // 菜单树选中
      handleTreeSelectChange (data, node) {
        this.dataForm.parentId = data.id;
        this.parentIdForSave = data.id;
        this.parentNameForSave  = data.name;
        this.dataForm.orgId = data.id; // 给新增或修改界面中javabean所属机构赋值
      },
      //头像上传
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        this.dataForm.userimg=res.data.imgNamePc;
        this.attachId=res.data.attachId;
      },
      //获取当前选中行
      selectionChange:function(params){
        this.selections=params.selections;
      },
      getUploadHeaders: function(){
        var token = Cookies.get("token");
        if (token){
          this.uploadHeaders.authorization = "Bearer" + token;
        }
      },
      beforeAvatarUpload(file) {
        let imgType=["image/jpeg","image/png","image/gif","image/bmp"];
        let isJPG=0;
        for(let j=0;j<imgType.length;j++){
          if(file.type === imgType[j]){
            isJPG = 1;
          }
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (isJPG!=1) {
          this.$message.error('上传头像图片只能是 JPG/PNG/GIF/BMP 格式!');
        }
        if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      //重置密码
      ResetPassword:function(){
        if(this.selections.length<1){
          this.$message({message: "请选择一条记录", type: 'warning'});
        }else{
          this.$confirm('确认重置密码吗？', '提示', {}).then(() => {
            let arr = [];
            for (let i=0; i<this.selections.length; i++){
              arr.push(this.selections[i].id);
            }
            this.$api.user.resetPassword(arr).then((res) => {
              if(res.code == 0) {
                this.$message({ message: '操作成功', type: 'success' })
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
              this.findPage(null)
            })
          });
        }
      },
      // 获取分页数据
      findPage: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest
        }
        let jj={};
        jj.sidx = "update_time";
        jj.order = "desc";
        jj.pageNum=this.pageRequest.pageNum;
        jj.pageSize=this.pageRequest.PageSize;
        jj.mobile=this.filters.mobile;
        jj.status=this.filters.status;
        jj.username=this.filters.username;
        jj.userCard=this.filters.userCard;
        jj.displayType=this.filters.displayType;
        jj.sex=this.filters.sex;
        jj.orgId=this.testTreeId;
        this.$api.user.findPage(jj).then((res) => {
          for(let i=0;i<res.data.list.length;i++){
            res.data.list[i].userimg=baseUrl+'/uploads/sys-user-img/'+res.data.list[i].userimg
            if(res.data.list[i].status=='正常'){
              res.data.list[i].status='1'
            }else{
              res.data.list[i].status='0'
            }
          }
          this.pageResult = res.data;
        }).then(data!=null?data.callback:'');
        var arr = [];
        var arrpost = [];
        this.$api.role.queryAll().then((res) => {
          if (res.data.length > 0){
            for (let i=0; i<res.data.length; i++){
              let role = res.data[i];
              var obj = {
                label : role.roleName,
                roleId : role.roleId,
                id : role.roleId

              };
              arr.push(obj);
            }
          }
          this.dataRole = arr;
        });
        this.$api.user.findPost().then((res) => {
          if(res.data!=null){
            for (let i=0; i<res.data.length; i++){
              let post = res.data[i];
              var obj1 = {
                label : post.postName,
                postId : post.postId,
                id : post.postId,
              };
              arrpost.push(obj1);
            }
            this.dataPostTree = arrpost;
          }
        });
      },
      // 批量删除
      handleDelete : function (data) {
        let arr = [];
        for (let i=0; i<data.params.length; i++){
          arr.push(data.params[i].id);
        }
        this.$api.user.batchDelete(arr).then(data!=null?data.callback:'')
      },
      // 显示新增界面
      handleAdd: function () {
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
        this.dialogVisible = true;
        this.operation = true;
        this.imageUrl='';
        this.parentNameForSave = '';
        this.dataForm = {
          userId:'',
          username :'',
          userRealname :'',
          password :'',
          orgId :this.testTreeId,
          orgIdList:[],
          postIdList:[],
          parentId:0,
          sex :"0",
          userimg:'',
          userType :"0",
          status:"1",
          userTheme:'',
          userCard:'',
          nativePlace:'',
          email :'',
          zip :'',
          birthday:'',
          nation:'',
          mobile:'',
        };
      },
      // 显示修改界面
      handleEdit: function (params) {
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
        this.dialogVisible = true;
        this.operation = false;
        this.dataForm = Object.assign({}, params.row);
        if (this.operation == false){
          var data = {userId:this.dataForm.userId};
          this.$api.user.findView(data.userId).then((res) => {
            this.imageUrl=baseUrl+'/uploads/sys-user-img/'+res.data.userimg;
            this.dataForm.orgId=res.data.orgId;
            this.dataForm.sex=res.data.sex;
            this.dataForm.status=res.data.status;
            this.dataForm.userType=res.data.userType;
            this.dataForm.userimg=res.data.userimg;
            this.parentNameForSave = res.mainOrgName;
            this.dataForm.orgIdList=[];
            this.popupTreeOrgIdList=[];
            for(let i=0;i<res.myData.length;i++){
              this.dataForm.orgIdList.push(res.myData[i].orgId)
              this.popupTreeOrgIdList.push(res.myData[i].orgId)
            }
            if(res.data.postIdList.length==0){
              this.postValue='';
              this.dataForm.postIdList=this.postValue;
            }else{
              this.postValue = res.data.postIdList[0].split(",")[0];
            }
          });
        }
        this.findPage(null);
      },
      // 修改
      submitForm:function () {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              let params = Object.assign({}, this.dataForm);
              if (this.dataForm.orgIdList == null){
                this.dataForm.orgIdList = [];
              }
              params.postIdList = [];
              params.postIdList.push(this.postValue); // 将所属岗位postId赋值
              params.orgIdList=this.dataForm.orgIdList;
              params.attachId = this.attachId;
              this.$api.user.save(params).then((res) => {
                this.editLoading = false
                if(res.code == 0) {
                  this.$message({ message: '操作成功', type: 'success' })
                  this.dialogVisible = false
                  this.$refs['dataForm'].resetFields()
                } else {
                  this.$message({message: '操作失败, ' + res.msg, type: 'error'})
                }
                this.findPage(null)
              })
            })
          }
        })
      },
      // 获取部门列表
      findDeptTree: function () {
        this.$api.dept.findDeptTree().then((res) => {
          this.deptData = res.data
        })
      },
      // 时间格式化
      dateFormat: function (row, column, cellValue, index){
        return format(row[column.property])
      },
      // 处理表格列过滤显示
      handleFilterColumns: function (data) {
        this.filterColumns = data.filterColumns
        this.$refs.tableColumnFilterDialog.setDialogVisible(false)
      },
      // 处理表格列过滤显示
      initColumns: function () {
        this.columns = [
          {prop:"username", label:"登录账号", minWidth:100},
          {prop:"sex", label:"性别", minWidth:70},
          {prop:"orgnames", label:"所属机构", minWidth:100},
          {prop:"userCard", label:"身份证", minWidth:100},
          {prop:"userType", label:"用户类型", minWidth:100},
          {prop:"mobile", label:"手机号码", minWidth:80},
        ];
        this.filterColumns = JSON.parse(JSON.stringify(this.columns));
      },
      //带搜索框的数
      filterNodeleft(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      // 机构树
      queryOrgTree : function () {
        this.$api.role.orgTree().then(res => {
          let attr = {
            id: 'orgId',
            name: 'orgName',
            parentId: 'parentId',
            parentName: 'parentName',
            rootId: "-1"
          };
          var treeData = this.$api.role.toTreeData(res.data, attr);
          this.popupTreeDataForQuery=treeData;
          this.dataleftTree = treeData;
          //所属副机构
          for(let i=0;i<treeData.length;i++){
            var obj = {
              id:treeData[i].id,
              label:treeData[i].label,
              children:treeData[i].children,
            };
            this.popupTreeDataTwo.push(obj);
          }
          this.dg(this.popupTreeDataTwo);
        });
      },
      //递归
      dg:function(data){
        for(let i=0;i<data.length;i++){
          data[i].value= data[i].id;
          if(data[i].children.length!=0){
            this.dg(data[i].children);
          } else if(data[i].children.length==0){
            delete data[i].children;
            delete data[i].list;
          }
        }
      },
      // 新增修改界面中所属机构,在点击由 clearable 属性生成的清空按钮时触发
      clearForSave(){
        this.parentNameForSave = null; // 清除所属机构input文本
        this.parentIdForSave = null; // 清除所属机构input值
        this.dataForm.orgId = null; // 清除值
      },
      focus : function(){}
    },
    mounted() {
      this.initColumns();
      this.queryOrgTree();
      this.getUploadHeaders();
      this.changeParams();
    },
  }
</script>

<style scoped>
  .avatar-uploader {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 150px;
    height: 150px;
    line-height: 150px;
    text-align: center;
  }
  .avatar {
    width: 150px;
    height: 150px;
    display: block;
  }

</style>










