      // 显示修改界面
      handleEdit: function (params) {

        this.tableVue = params.tableVue;
        this.tableVue.clearSelection(); // 先清空所有的选择
        this.tableVue.toggleRowSelection(params.row); // 重新选中当前行

        this.dialogVisible = true; // 显示界面
        this.operation = false; // 标识为修改
        this.dataForm = Object.assign({}, params.row);
        // 数据明细
        if (this.dataForm.dataScope=="5"){
          this.queryAddOrgTree();
        } else {
          this.dataleftTree = [];
        }
        // 角色状态
        if ("启用" == this.dataForm.status){
          this.dataForm.status = 1;
        } else if ("停用" == this.dataForm.status){
          this.dataForm.status = 0;
        }
        // 修改的时候，需要查询该角色的明细
        if (this.operation == false){
          var data = {roleId:this.dataForm.roleId};
          this.$api.role.selectObjectById(data).then((res) => {
            // 处理菜单树
            if (res.data.tsysRole.menuIdList != null && res.data.tsysRole.menuIdList.length > 0){
              // 默认先赋值菜单ID集和机构ID集
              this.dataForm.menuIdList = res.data.tsysRole.menuIdList;
              this.dataForm.orgIdList = res.data.tsysRole.orgIdList;
              var _arr = [];
              for(var i=0; i<res.data.tsysRole.menuIdList.length; i++){
                var _obj = {id:res.data.tsysRole.menuIdList[i]};
                _arr.push(_obj);
                this.$refs.menuTree.setChecked(_arr[i].id, true, false); // 通过setChecked设置节点选中
              }
            }
            // 处理用户机构 todo
            this.dataForm.orgId = res.data.tsysRole.orgId; // 将机构赋值给dataForm中的orgId
            this.parentNameForSave = res.data.orgName; //
            // 处理数据防卫【按明细设置】的机构数
            if (res.data.tsysRole.dataScope == "5"){
              var _arr = res.data.tsysRole.orgIdList;
              this.$refs.leftTree.setCheckedKeys(_arr); // 设置节点选中
            }
          });
        }
        // 清除验证
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
      },

