      // 保存 执行数据新增或修改
      submitForm: function () {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.dialogVisible = false; // false关闭界面
              this.editLoading = true;
              if (this.dataForm.dataScope != "5") { // 当数据范围不是【按明细设置】时，清空该值
                this.dataForm.orgIdList = [];
              }
              let params = Object.assign({}, this.dataForm);
              this.$api.role.save(params).then((res) => {
                this.editLoading = false
                if(res.code == 0) {
                  this.$message({ message: '保存成功', type: 'success' })
                  this.dialogVisible = false
                  this.$refs['dataForm'].resetFields();
                  // 清除
                  this.parentNameForSave = '';
                  // 清除数据范围中机构数选择值
                  this.dataleftTree = [];
                  this.$refs.leftTree.setCheckedKeys([]);
                } else {
                  this.$message({message: '保存失败, ' + res.msg, type: 'error'})
                }
                this.findPage(null)
              });
            })
          }
        })
      },
