<template>
  <div>
    <el-container class="page-container scrollAllY ">
      <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
        <!--工具栏-->
        <el-form class="queryForm" label-width="100px"  ref="queryForm" :inline="true" :model="filters" :size="size" label-position="right">
          <el-row class="elCollapseDict">
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span>岗位类型</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item  class="dictInputQueryWidth textAlign">
                <el-select  class="widthAll"  v-model="filters.postType" placeholder="请选择" clearable>
                  <el-option class="dictQueryOptionPadding" v-for="item in queryFormDefaultOptions"
                             :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span>岗位名称</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth textAlign">
                <el-input    v-model="filters.postName"   maxlength="50" type="text"  placeholder="岗位名称" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="elDeptFormButton dictQueryCenterButton">
            <el-col :span="24">
              <cb-button icon="fa fa-search" :label="$t('action.search')"
                         perms="sys:tsyspost:query"
                         type="primary" @click="findPages(null)"/>
              <cb-button icon="fa fa-plus" :label="$t('action.add')"
                         type="primary" @click="handleAdd" perms="sys:tsyspost:add"  />
            </el-col>
          </el-row>

        </el-form>
      </el-header>
      <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
        <!--表格内容栏-->
        <cb-table permsEdit="sys:tsyspost:edit" permsDelete="sys:tsyspost:remove"
                  permsMove="sys:tsyspost:query"  :postShowType="true"
                  :data="pageResult" :columns="columns"  :queryFormDefault="queryFormDefault"
                  :isOpen="isOpen"  row-key="postId"
                  @findPage="findPages" @pageSizes="pageSizes"
                  @toggleRowSelection="toggleRowSelection"
                  @handleMove="handleMove"
                  @selectionChange="selectionChange" @handleEdit="handleEdit"
                  @handleDeleteOther="handleDelete">
        </cb-table>
      </el-main>
    </el-container>
    <!--新增修改界面-->
    <el-dialog v-dialogDrag :title="operation?$t('action.add')+'岗位':$t('action.edit')+'岗位'" width="30%"
               :visible.sync="dialogVisible" :close-on-click-modal="false">
      <el-form :model="dataForm" label-width="100px" :rules="dataRule" ref="dataForm"
               :size="size" class="formInfo dialogFormTop" @keyup.enter.native="submitForm()" >
          <el-form-item label="ID" prop="postId" v-if="false">
            <el-input v-model="dataForm.postId" :disabled="true" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="岗位类型" prop="postType">
            <el-select  class="widthAll"   v-model="dataForm.postType" placeholder="请选择" clearable>
              <el-option class="dictQueryOptionPadding" v-for="item in queryFormDefault" :key="item.value" :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="岗位名称" prop="postName">
            <el-input v-model="dataForm.postName"  maxlength="50" type="text" clearable auto-complete="off"></el-input>
          </el-form-item>
        <el-form-item  label="排序编号" prop="sort" >
          <el-input-number v-model="dataForm.sort"
                           controls-position="right"  :min="1" :max="99999" label="排序编号"  class="widthAll">
          </el-input-number>
        </el-form-item>
          <!--<el-form-item label="上级岗位" prop="parentPostid" >
            <el-popover placement="bottom-start"  trigger="click" > &lt;!&ndash; hover &ndash;&gt;
              <el-tree :data="tableTreeDdataDept" :props="treePropsDept" ref="popupTreeForSave"  node-key="postId"
                       @current-change="handleTreeSelectChangeDept" :default-expanded-keys="defaultExpandedKey"
              ></el-tree>
              <el-input v-model="dataForm.parentName==null||dataForm.parentName==''?'':dataForm.parentName"
                        slot="reference" style="cursor:pointer;" placeholder="点击选择所属上级岗位"
                        :clearable="true" @clear="clear" @focus="inputFocusTree"
              ></el-input>
            </el-popover>

            &lt;!&ndash;<popup-tree-input :clear="function() {}"   placement="bottom-start"
                              :data="tableTreeDdataDept" :props="treePropsDept"
                              :prop="dataForm.parentName==null||dataForm.parentName==''?'':dataForm.parentName"
                              :nodeKey="''+dataForm.postId"  :clearable="true"
                              :currentChangeHandle="handleTreeSelectChangeDept" :inputFocus="inputFocusTree" >
            </popup-tree-input>&ndash;&gt;
          </el-form-item>-->
          <el-form-item label="岗位描述" prop="remark">
            <el-input v-model="dataForm.remark" type="textarea" maxlength="100" clearable :rows="1" auto-complete="off"></el-input>
          </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :size="size" @click="dialogVisible = false">{{$t('action.cancel')}}</el-button>
        <el-button :size="size" type="primary" @click="submitForm({continue:'1'})" >{{$t('action.saveContinue')}}</el-button>
        <el-button :size="size" type="primary" @click="submitForm" >{{$t('action.preservation')}}</el-button>

      </div>
    </el-dialog>
  </div>

</template>

<script>
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import TableTreeColumn from "@/views/Core/TableTreeColumn";
  import { format } from "@/utils/datetime"
  import PopupTreeInput from "@/components/PopupTreeInput"
  export default {
    components:{
      CbTable,
      CbButton,
      TableTreeColumn,
      PopupTreeInput
    },
    data() {
      var validateName=(rule, value, callback) => {
        value=$.trim(value);
        if (!value) {
          return callback(new Error('岗位名称不能为空'));
        }else{
          callback();
        }
      };
      return {
        tableTreeDdataDept:[],
        defaultExpandedKey:[],
        treePropsDept:{
          label: 'postName',
          children: 'children'
        },
        tableTreeDdata:[],
        operation: false, // true:新增, false:修改
        dialogVisible: false, // 新增修改界面是否显示
        isOpen : true, // 是否开启点击表格行也选中
        queryFormDefaultOptions:[
          {value: '',label: '全部' },
          {value: '0',label: '公司领导' },
          {value: '1',label: '普通员工' }
        ],
        queryFormDefaultType:['','success','info','warning','danger'],
        queryFormDefault:[
          {value: '0',label: '公司领导',type:'' },
          {value: '1',label: '普通员工' ,type:'success'}
        ],
        size: 'small',
        filters: {
          postType: "",
          postName:""
        },
        filterColumns:[],
        columns: [
          {prop:"postName", label:"岗位名称", minWidth:60},
          /*{prop:"postType", label:"岗位类型", minWidth:70},*/
          {prop:"sort", label:"排序号", minWidth:50},
          {prop:"remark", label:"岗位描述", minWidth:80},
          {prop:"createUserId", label:"创建人", minWidth:70},
          {prop:"createTime", label:"创建时间", minWidth:90},
          {prop:"updateUserId", label:"修改人", minWidth:70},
          {prop:"updateTime", label:"修改时间", minWidth:90}
        ],
        pageRequest: {
          totalCount: 188,//总共多少条数据
          PageSize: 10,//一页显示几条  limit
          totalPage: 63,//有多少页
          pageSizes:[10, 20, 30, 40],
          currPage: 1,//当前页码  page
          pagerCount:5,//显示几个页数按钮
        },
        loading: false,
        dataRule: {
          postName: [{ validator: validateName,required: true, message: '岗位名称不能为空', trigger: 'blur' } ],
          postType: [{ required: true, message: '岗位类型不能为空', trigger: 'blur' } ]
        },
        // 新增修改界面数据
        dataForm: {
          postId:'', // 角色id
          postType:'1', // 角色名称
          postName:'',
          parentPostid:'',
          sort:'',
          remark:'',
          parentName:''
        },
        pageSizes:[10, 20, 30, 40],
        pageResult: {},
        selections:{ // 列表选中列
          select:[],
          dataForm:{},
        }

      }
    },
    methods: {
      clear(){
        this.dataForm.parentPostid='';
        this.dataForm=Object.assign({}, this.dataForm);
      },
      //被选中的列
      handleRadioSelect(row) {
        this.selections.select=[];
        this.selections.select.push(row.postId);
        this.selections.dataForm={};
        this.selections.dataForm=Object.assign(this.selections.dataForm, row);
      },
      // 获取上级菜单树
      getParentMenuTree: function(tableTreeDdata) {
        let parent = {
          parentPostid: 0,
          name: "顶级岗位",
          children: tableTreeDdata
        };
        return [parent];
      },
      //获取岗位树
      inputFocusTree(data, node) {
        this.$api.post.findAll().then(res => {
          if(res.data){
            this.defaultExpandedKey=[];
            for(let i=0;i<res.data.length;i++){
              this.defaultExpandedKey.push(res.data[i].postId);
              if(res.data[i].children){
                for(let k=0;k<res.data[i].children.length;k++){
                  this.defaultExpandedKey.push(res.data[i].children[k].postId);
                }
              }
            }
          }
          this.tableTreeDdataDept=res.data;
        });
      },
// 菜单树选中
      handleTreeSelectChangeDept(data, node) {
        this.dataForm.parentPostid = data.postId;
        this.dataForm.parentName = data.postName;
      },
      // 获取分页数据
      findPages: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest
        }
        this.filters.page=this.pageRequest.pageNum;
        this.filters.limit=this.pageRequest.PageSize;
        this.filters.sidx = "sort";  this.filters.order = "asc";
        this.$api.post.findPage(this.filters).then(res => {
          this.pageResult=res.data;
        }).then(data!=null?data.callback:'');

      },
      // 显示新增界面
      handleAdd: function () {

        this.dialogVisible = true;
        this.operation = true; // 标识为新增
        this.dataForm = {
          postId:'', // 角色id
          postType:'1', // 角色名称
          postName:'',
          parentName:'',
          parentPostid:'',
          sort:'',
          remark:''
        };
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
        this.$api.post.getMaxSort().then((res) => {
          if (res.code == 0){
            this.dataForm.sort = res.data+1;
          }
        });
      },
      // 保存
      submitForm: function (continues) {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            let params = Object.assign({}, this.dataForm);
            this.$confirm('确认提交吗？', '提示', { closeOnClickModal:false,type: "warning" }).then(() => {
              this.$api.post.save(params).then((res) => {
                if(res.code === 0) {
                  this.$message({ message: '操作成功', type: 'success' });
                 /* this.findDataPostType();*/
                  if(continues.continue){  /*  */
                    this.dataForm.postId=null;
                    this.dataForm= Object.assign({}, this.dataForm);
                  }else{
                    this.dialogVisible = false;
                    this.findPages(null);
                    this.$refs['dataForm'].resetFields();
                  }
                } else {
                  this.$message({message: '操作失败, ' + res.msg, type: 'error'});
                  this.dialogVisible = false;
                  this.$refs['dataForm'].resetFields();
                }

              })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消提交'
              });
            });
          }
        })
      },
      // 显示修改界面
      handleEdit: function (row) {

          this.dialogVisible = true;
          this.operation = false; // 标识为修改
          this.dataForm=Object.assign(this.dataForm, row.row);
        for(let i=0;i<this.queryFormDefault.length;i++){
          if(this.dataForm.postType===this.queryFormDefault[i].label){
            this.dataForm.postType=this.queryFormDefault[i].value;
          }
        }
        this.dataForm=Object.assign({}, this.dataForm);
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
      },
      handleMove:function(data){
        let jj={};
        jj.currPostId=data.row.postId;
        if(data.moves.moveUp){
          jj.targetPostId=this.pageResult.list[data.index-1].postId;

          this.$confirm("确认上移选中记录吗？", "提示", {
            type: "warning",
            closeOnClickModal:false
          }).then(() => {
            this.$api.post.postMove(jj).then(res => {
              if(res.code===0){
                this.$message({ message: "上移成功", type: "success" });
              }else{
                this.$message.error(res.msg);
                return;
              }
              this.findPages(null);

            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消上移'
            });
          });
        }
        if(data.moves.moveDown){
          jj.targetPostId=this.pageResult.list[data.index+1].postId;

          this.$confirm("确认下移选中记录吗？", "提示", {
            type: "warning",
            closeOnClickModal:false
          }).then(() => {
            this.$api.post.postMove(jj).then(res => {
              if(res.code===0){
                this.$message({ message: "下移成功", type: "success" });
              }else{
                this.$message.error(res.msg);
                return;
              }
              this.findPages(null);
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消下移'
            });
          });
        }
      },
      // 批量删除
      handleDelete: function (data) {

        if(data.obj){
          let jj=[];
          jj.push(data.obj.postId);
          console.log("132");
          console.log(jj);
          this.delete(jj);
        }else{
          if(data.params){
            let jj=[];
            for(let k=0;k<data.params.length;k++){
              jj.push(data.params[k].postId);
            }
            this.delete(jj);
          }
        }
      },
      delete:function(jj){
        this.$confirm("确认删除选中记录吗？", "提示", {
          type: "warning",
          closeOnClickModal:false
        }).then(() => {
          this.$api.post.batchDelete(jj).then(res => {
            if(res.code===0){
              this.$message({ message: "删除成功", type: "success" });
            }else{
              this.$message.error(res.msg);
              return;
            }
            this.findPages(null);
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      // 获取删除的包含子菜单的id列表
      getDeleteIds(ids, row) {
        ids.push(row.postId);
        if (row.children != null) {
          for (let i = 0, len = row.children.length; i < len; i++) {
            this.getDeleteIds(ids, row.children[i]);
          }
        }
        return ids;
      },
      //获取当前选中行
      selectionChange:function(params){
        this.selections=params.selections;
      },
      toggleRowSelection(result) {
        this.selections=[];
        this.selections = result.list;
      },
      //初始化数据
      findDataPostType:function(){
        this.loading = true;
        this.$api.post.findAll(this.filters).then(res => {
          this.tableTreeDdata = res.data;
         /* this.popupTreeData = this.getParentMenuTree(res.data);*/
          this.loading = false;
        });
        this.$api.parameter.findParaType({paraType:'postType'}).then(res => {
          if(res.data){
            this.queryFormDefaultOptions=[];
            this.queryFormDefault=[];
            let j=0;
            for(let i=0;i<res.data.length;i++){
              let para={};
              para.value=res.data[i].parano;
              para.label=res.data[i].paraKey;
              if(j>this.queryFormDefaultType.length){
                j=0;
              }
              para.type=this.queryFormDefaultType[j];
              j++;

              this.queryFormDefaultOptions.push(para);
              this.queryFormDefault.push(para);
            }
          }
        });

        this.filterColumns = JSON.parse(JSON.stringify(this.columns));
      }

    },
    mounted() {
      this.findDataPostType();
    }
  }
</script>

<style scoped>

</style>



