<template>
    <el-container class="page-container scrollAllY">
        <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
          <!--工具栏-->
          <el-form class="textAlign" label-width="100px" ref="queryForm" :inline="true" :model="filters" :size="size"  label-position="right">
            <el-collapse class="elCollapseItemNoBoeder elCollapseDict">
              <el-row>
                <el-col :span="2">
                  <el-form-item class="dictInputQueryLabelWidth">
                    <span>登录时间</span>
                  </el-form-item>
                </el-col>
                <el-col :span="14">
                  <el-form-item  class="dictInputQueryWidth textAlign">
                    <el-date-picker
                      v-model="timeAll"
                      class="widthAll"
                      type="datetimerange"
                      :picker-options="pickerOptions"
                      value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm:ss"
                      range-separator="至"
                      start-placeholder="登录开始时间"
                      end-placeholder="登录结束时间"
                      align="right">
                    </el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="2">
                  <el-form-item class="dictInputQueryLabelWidth">
                    <span>日志名称</span>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item  class="dictInputQueryWidth textAlign">
                    <el-input  v-model="filters.logname" placeholder="日志名称" clearable></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-collapse-item class="elCollapseItemNoBoeder elCollapseItemMarginBottom">
                <el-row>
                  <el-col :span="2">
                    <el-form-item class="dictInputQueryLabelWidth">
                      <span>是否执行成功</span>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  class="dictInputQueryWidth textAlign">
                      <el-input  v-model="filters.succeed" placeholder="是否执行成功" clearable></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="2">
                    <el-form-item class="dictInputQueryLabelWidth">
                      <span>登录ip</span>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  class="dictInputQueryWidth textAlign">
                      <el-input  v-model="filters.ip" placeholder="登录ip" clearable></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="2">
                    <el-form-item class="dictInputQueryLabelWidth">
                      <span>操作员名称</span>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  class="dictInputQueryWidth textAlign">
                      <el-input  v-model="filters.userid" placeholder="操作员名称" clearable></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
              </el-collapse-item>
            </el-collapse>
            <el-row class="elDeptFormButton dictQueryCenterButton">
              <el-col :span="24">
                <cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:tsysloginlog:query" type="primary" @click="findPage(null)"/>
              </el-col>
            </el-row>

          </el-form>
        </el-header>
        <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
          <!--表格内容栏-->
          <cb-table  permsDelete="sys:tsysloginlog:remove" :data="pageResult" :columns="columns"  @selectionChange="selectionChange"
                    :isOpen="isOpen" :cbid="cbid"
                    @findPage="findPage" @handleDeleteOther="handleDeleteOther" @handleBatchDelete="handleBatchDelete">
          </cb-table>
        </el-main>
    </el-container>
</template>

<script>
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import { format } from "@/utils/datetime"
  export default {
    components: {
      CbTable,
      CbButton
    },
    data() {
      return {
        timeAll:[],
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        selections : [],
        isOpen : true,
        cbid : 'loginlogid',
        ids : [], // 该值用于存值被删除的id
        size: 'small',
        filters: {
          logname:'',
          succeed:'',
          ip:'',
          makeName:'',
        },
        dataForm:{
          logname:'',
          succeed:'',
          ip:'',
          message:'',
          userid:'',
          makeName:'',
        },
        columns: [
          {prop:"logname", label:"日志名称", minWidth:100},
          {prop:"userid", label:"操作人名称", minWidth:100},
          {prop:"createTime", label:"登录时间", minWidth:100},
          {prop:"succeed", label:"是否执行成功", minWidth:100},
          {prop:"message", label:"具体消息", minWidth:100},
          {prop:"ip", label:"登录Ip", minWidth:100},
        ],
        pageResult: {},
      }
    },
    methods: {
      // 获取分页数据
      findPage: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest
        }
        let jj={};
        jj.page = this.pageRequest.pageNum; // 第几页
        jj.limit = this.pageRequest.PageSize; // 每页显示数
        jj.logname = this.filters.logname; // 日志名称
        jj.succeed = this.filters.succeed; // 是否成功
        jj.ip = this.filters.ip;
        jj.userid = this.filters.userid; // 操作人名称
        if(this.timeAll){
          jj.beginTime=this.timeAll[0];
          jj.endTime=this.timeAll[1];
        }
        this.$api.loginlog.findPage(jj).then((res) => {
          this.pageResult = res.data;
        }).then(data!=null?data.callback:'');
      },

      selectionChange:function(data){
        var selections = data.selections;
        if (selections.length > 0){
          for(var i=0; i<selections.length; i++){
            var obj = selections[i];
            this.ids.push(obj.loginlogid);
          }
        }
      },

      // 删除
      handleDeleteOther: function (data) {
        data.vue.clearSelection();
        data.vue.toggleRowSelection(data.obj);
        this.$confirm('确认删除选中记录吗？', '提示', {
          type: 'warning',
          closeOnClickModal:false
        }).then((res) => {
          if (data.obj != undefined && data.obj != null){
            this.ids.push(data.obj.loginlogid);
          }
          this.$api.loginlog.remove(this.ids).then((res) => {
            if (res.code == 0){
              this.$message({message: '删除成功', type: 'success'});
              this.findPage(null);
            } else {
              this.$message({message: '批量删除失败', type: 'error'});
            }
          });
          this.ids = [];
        }).catch(() => {
          data.vue.toggleRowSelection(data.obj);
        });;
      },
      // 批量删除
      handleBatchDelete:function(data){
        /*
        var selections = data.params;
        this.ids = selections.map(item => item.loginlogid).toString().split(",");
        //this.$api.loginlog.remove(this.ids).then(data!=null?data.callback:'');
        this.$api.loginlog.remove(this.ids).then((res) => {
          if (res.code == 0){
            this.$message({message: '批量删除成功', type: 'success'});
            this.findPage(null);
          } else {
            this.$message({message: '批量删除失败', type: 'error'});
          }
        });
        */
      },

      // 时间格式化
      dateFormat: function (row, column, cellValue, index){
        return format(row[column.property])
      }
    },
    mounted() {
    }
  }
</script>

<style scoped>

</style>



