<template>
  <el-container class="page-container scrollAllY">
    <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
      <!--工具栏-->
      <el-form class="queryForm" label-width="100px" ref="queryForm" :inline="true" :model="filters" :size="size">
        <el-row class="elCollapseDict">
          <el-col :span="2">
            <el-form-item class="dictInputQueryLabelWidth">
              <span>用户名</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item class="dictInputQueryWidth ">
              <el-input  v-model="filters.name" placeholder="用户名" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-form-item class="dictInputQueryLabelWidth">
              <span>用户操作</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item class="dictInputQueryWidth ">
              <el-input  v-model="filters.operation" placeholder="用户操作" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-form-item class="dictInputQueryLabelWidth">
              <span>IP</span>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item class="dictInputQueryWidth ">
              <el-input  v-model="filters.ip" placeholder="IP" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row class="elDeptFormButton dictQueryCenterButton">
          <el-col :span="24">
            <cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:tsyslog:query" type="primary" @click="findPage(null)"/>
          </el-col>
        </el-row>
      </el-form>
    </el-header>
    <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
      <!--表格内容栏-->
      <cb-table permsDelete="sys:tsyslog:remove"  :data="pageResult" :columns="columns" :isOpen="isOpen"
                @findPage="findPage" @handleDelete="handleDelete">
      </cb-table>
    </el-main>
  </el-container>
</template>
<script>
import CbTable from "@/views/Core/CbTable"
import CbButton from "@/views/Core/CbButton"
import { format } from "@/utils/datetime"
export default {
	components: {
		CbTable,
		CbButton
	},
	data() {
		return {
			size: 'small',
			filters: {
				name: ''
			},
			columns: [
				{prop:"username", label:"用户名", minWidth:70},
				{prop:"operation", label:"用户操作", minWidth:130},
				{prop:"ip", label:"IP", minWidth:70},
        {prop:"createDate", label:"创建时间", minWidth:120},
				{prop:"logType", label:"日志类型", minWidth:70},
				{prop:"timeConsuming", label:"请求耗时", minWidth:70},
				{prop:"requestUri", label:"请求URI", minWidth:130, }
			],
      pageResult: {},
      isOpen : true, // 是否开启点击表格行也选中
		}
	},
	methods: {
    rowClick(){},
		// 获取分页数据
		findPage: function (data) {
			if(data !== null) {
				this.pageRequest = data.pageRequest
			}
      let jj={};
      jj.pageNum=this.pageRequest.pageNum;
      jj.pageSize=this.pageRequest.PageSize;
      jj.username=this.filters.name;
      jj.operation=this.filters.operation;
      jj.ip=this.filters.ip;
			this.$api.log.findPage(jj).then((res) => {
				this.pageResult = res.data;
			}).then(data!=null?data.callback:'')
		},
    // 批量删除
    handleDelete: function (data) {
      let arr = [];
      for (let i=0; i<data.params.length; i++){
        arr.push(data.params[i].id);
      }
      this.$api.log.doDelete(arr).then(data!=null?data.callback:'')
    },
		// 时间格式化
    dateFormat: function (row, column, cellValue, index){
      	return format(row[column.property])
   	}
	},
	mounted() {
	}
}
</script>

<style scoped>

</style>



