<template>
  <div class="page-container">
    <el-container class="page-container scrollAllY">
      <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
        <!--工具栏-->
        <el-form class="queryForm" label-width="100px" ref="queryForm" :inline="true" :model="filters" :size="size">
          <el-row class="elCollapseDict">
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span>Bean名称</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <el-input  v-model="filters.beanName" placeholder="Bean名称" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span> 方法名称</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <el-input  v-model="filters.methodName" placeholder="方法名称" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="elDeptFormButton dictQueryCenterButton">
            <el-col :span="24">
              <cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:tsysserialno:query" type="primary" @click="findPage(null)"/>
              <!--<cb-button icon="fa fa-search" :label="$t('action.add')" perms="sys:tsysserialno:query" type="primary" @click="add()"/>-->
              <!--<cb-button icon="fa fa-trash-o" :label="$t('action.batchDelete')" perms="sys:tsysdict:remove"  type="primary" @click="handleDeleteBatch()"/>-->
              <cb-button icon="fa fa-lemon-o" :label="$t('action.stop')"  perms="sys:tsysserialno:query" type="primary" @click="handleStop" />
              <cb-button icon="fa fa-shower" :label="$t('action.recovery')"  perms="sys:tsysserialno:query" type="primary" @click="handleRecovery" />
              <cb-button icon="fa fa-flag-o" :label="$t('action.execute')" perms="sys:tsysserialno:query"  type="primary" @click="handleExecute" />
            </el-col>
          </el-row>
        </el-form>
      </el-header>
      <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
        <!--表格内容栏-->
        <cb-table ref="cbtable" permsEdit="sys:job:edit" permsDelete="sys:job:remove"
                  @pageSizes="pageSizes" :data="pageResult" :columns="columns"  :isShow="isShow" :isOpen="isOpen" :border="border" :selectable="selectable"
                  @findPage="findPage" @handleEdit="handleEdit" @handleDeleteOther="handleDeleteOther" :isShowBatchDelete="isShowBatchDelete"
                  @selectionChange="selectionChange" @switchChange="switchChange" @toggleRowSelection="toggleRowSelection">
        </cb-table>
      </el-main>
    </el-container>
    <!--修改界面-->
    <el-dialog v-dialogDrag title="修改定时任务" width="40%" :visible.sync="dialogVisible" :close-on-click-modal="false" @close="dialogClosForSave">
      <el-form :model="dataForm" ref="dataForm" label-width="100px" :size="size" style="display: flex;height: 130px;margin-top: 10px" :rules="dataJob">
        <div style="width: 50%;">
          <el-form-item label="ID" prop="jobId" v-if="false">
            <el-input v-model="dataForm.jobId" :disabled="true" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="Bean名称" prop="beanName">
            <el-input v-model="dataForm.beanName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="参数" prop="params">
            <el-input v-model="dataForm.params" auto-complete="off"></el-input>
          </el-form-item>
        </div>
        <div style="width: 50%;margin-right: 40px">
          <el-form-item label="方法名称" prop="methodName">
            <el-input v-model="dataForm.methodName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="cron表达式" prop="cronExpression">
            <el-input v-model="dataForm.cronExpression" auto-complete="off"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <el-form :model="dataForm" label-width="100px"  :size="size" style="margin-top: -20px">
        <el-form-item label="备注说明" prop="remark">
          <el-input type="textarea" v-model="dataForm.remark" auto-complete="off" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :size="size" @click.native="dialogVisible = false">{{$t('action.cancel')}}</el-button>
        <el-button :size="size" type="primary" @click.native="submitForm"  :loading="editLoading">{{$t('action.preservation')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import { format } from "@/utils/datetime"
  export default {
    components:{
      CbTable,
      CbButton
    },
    data() {
      return {
        tableVue : {}, // 表格组件
        selections : [], // 表格中当前选中项
        ids : [], // 表格中被选中列的id
        editLoading : false, // 是否开启提交的动画
        dialogVisible: false, // 新增修改界面是否显示
        isShow : true, // 是否开启Switch 开关
        isOpen : true, // 是否开启表格行点击选中
        isShowBatchDelete : false, // 是否显示右下角批量删除按钮
        border : false, // 是否显示表格边框
        pageSizes : [], // 分页
        size : "small",
        // 查询条件
        filters: {
          beanName:'', // bean名称
          methodName:'', // 方法名称
        },
        // 表单数据
        dataForm:{
          jobId:'',
          beanName: '',
          methodName: '',
          cronExpression: '',
          params: '',
          remark: '',
          status: '',
        },
        // 表格数据
        pageResult:{},
        columns: [
          {prop:"beanName", label:"Bean名称", minWidth:100},
          {prop:"methodName", label:"方法名称", minWidth:100},
          {prop:"cronExpression", label:"cron表达式", minWidth:120},
          {prop:"params", label:"参数", minWidth:100},
          {prop:"remark", label:"备注", minWidth:100}
        ],
        // 规则验证
        dataJob: {
          beanName: [{ required: true, message: "Bean名称不能为空", trigger: "blur" }],
          methodName: [{ required: true, message: "方法名称不能为空", trigger: "blur" }],
          params: [{ required: true, message: "参数不能为空", trigger: "blur" }],
          cronExpression: [{ required: true, message: "cron表达式不能为空", trigger: "blur" }],
        }
      }
    },
    methods: {

      // 获取分页数据
      findPage: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest;
        }
        var params = {};
        params.page = this.pageRequest.pageNum; // 当前页
        params.limit = this.pageRequest.PageSize; // 每页显示树
        params.beanName = this.filters.beanName; // bean名称
        params.methodName = this.filters.methodName; // 方法名称
        this.$api.job.findPage(params).then((res) => {
          var list = res.data.list;
          for(var i=0; i<list.length; i++){
            // 应为数据库里的值0为正常1为暂停
            if (list[i].status == 0){
              list[i].status = 1;
            } else if (list[i].status == 1){
              list[i].status = 0;
            }
            list[i].status = list[i].status.toString(); // 转为字符串，switch配置的是需要字符串的
          }
          this.pageResult = res.data;
        }).then(data!=null?data.callback:'');
      },

      // 暂停
      handleStop:function(){
        if (this.ids.length == 0 || this.ids == null){
          this.$message({message : '请至少选择一项', type : 'warning'});
          return false;
        }
        this.$api.job.pause(this.ids).then((res) => {
          if (res.code == 0){
            this.$message({message : '暂停成功', type : 'success'});
          } else {
            this.$message({message : '暂停失败', type : 'error'});
          }
        });
      },

      // 恢复
      handleRecovery:function(){
        if (this.ids.length == 0 || this.ids == null){
          this.$message({message : '请至少选择一项', type : 'warning'});
          return false;
        }
        this.$api.job.resume(this.ids).then((res) => {
          if (res.code == 0){
            this.$message({message : '恢复成功', type : 'success'});
          } else {
            this.$message({message : '恢复失败', type : 'error'});
          }
        });
      },

      // 立即执行
      handleExecute:function(){
        if (this.ids.length == 0 || this.ids == null){
          this.$message({message : '请至少选择一项', type : 'warning'});
          return false;
        }
        this.$api.job.runTask(this.ids).then((res) => {
          if (res.code == 0){
            this.$message({message : '执行成功', type : 'success'});
          } else {
            this.$message({message : '执行失败', type : 'error'});
          }
        });
      },

      // 删除
      handleDeleteOther: function (data) {

        this.tableVue = data.vue;
        this.tableVue.clearSelection(); // 先清空所有的选择
        this.tableVue.toggleRowSelection(data.obj); // 重新选中当前行

        let jobId = data.obj.jobId;
        this.$confirm('确认删除选中记录吗？', '提示', {
          type: 'warning',
          closeOnClickModal:false
        }).then((res) => {
          this.tableVue.toggleRowSelection(data.obj);
          this.$api.job.deletes([jobId]).then((res) => {
            if (res.code == 0){
              this.$message({message : '删除成功', type : 'success'});
              this.findPage(null);
            } else {
              this.$message({message : '删除失败', type : 'error'});
            }
          });
        }).catch(() => {
          this.tableVue.toggleRowSelection(data.obj); // 切换表格中当前行选中效果
        });
      },

      // 批量删除
      handleDeleteBatch:function(data){
        if (this.ids.length == 0 || this.ids == null){
          this.$message({message : '请至少选择一项', type : 'warning'});
          return false;
        }
        this.$api.job.deletes(this.ids).then((res) => {
          if (res.code == 0){
            this.$message({message : '删除成功', type : 'success'});
          } else {
            this.$message({message : '删除失败', type : 'error'});
          }
        });
      },

      add(){
        this.dialogVisible = true; // true显示修改界面
        var data = {};
        data.jobId = "";
        data.beanName = "testTask";
        data.methodName = "test";
        data.cronExpression = "0 0/30 * * * ?";
        data.params = "ossbar";
        data.remark = "我正测试中";
        data.status = 1;
        this.$api.job.save(data).then((res) => {
          console.log(res);
        });
      },

      // 显示修改界面
      handleEdit: function (params) {
        this.dialogVisible = true; // true显示修改界面
        // 填充对象
        this.dataForm.jobId = params.row.jobId; // 主键ID
        this.dataForm.beanName = params.row.beanName; // bean名称
        this.dataForm.methodName = params.row.methodName; // 方法名称
        this.dataForm.params = params.row.params; // 参数
        this.dataForm.cronExpression = params.row.cronExpression; // cron表达式
        this.dataForm.remark = params.row.remark; // 备注
        this.dataForm.status = params.row.status; // 状态

        this.tableVue = params.tableVue;
        this.tableVue.clearSelection(); // 先清空所有的选择
        this.tableVue.toggleRowSelection(params.row); // 重新选中当前行

      },

      // 新增或修改界面的关闭事件
      dialogClosForSave(){
        // 切换表格中的列选中效果
        this.tableVue.clearSelection(); // 用于多选表格，清空用户的选择
        this.selections = []; // 清除表格选中项
        // 清除验证
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }

      },

      // 保存
      submitForm: function () {
        this.$refs.dataForm.validate((valid) => {
          if (valid){ // 验证通过
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.editLoading = true; // 进入状态
              var data = Object.assign({}, this.dataForm);
              data.status = Number(data.status);
              // 因为前面已经换过了，这里需要换回来
              if (data.status == 0 || data.status == "0"){
                data.status = 1;
              } else if(data.status == 1 || data.status == "1"){
                data.status = 0;
              }
              this.$api.job.save(data).then((res) => {
                if (res.code == 0){
                  this.editLoading = false;
                  this.$message({ message: '保存成功', type: 'success' });
                  this.findPage(null);
                } else {
                  this.editLoading = false;
                  this.$message({ message: '保存失败', type: 'error' });
                }
              });
              this.dialogVisible = false; // false关闭界面
            });
          }
        });
      },

      // 时间格式化
      dateFormat: function (row, column, cellValue, index){
        return format(row[column.property])
      },

      // 当表格选择项发生变化时会触发该事件
      selectionChange : function(result){
        this.tableVue = result.vue;
        this.selections = result.selections;
        var ids = result.selections.map(item => item.jobId).toString();
        if (ids != "" && ids != null && ids != undefined){
          this.ids = ids.split(",");
        } else {
          this.ids = [];
        }
      },

      // 用于多选表格，切换某一行的选中状态
      toggleRowSelection(result){
        if (result.column == "操作"){
          //this.$refs.cbtable.rowClick(result.row, result.column);
        }
        //this.$refs.cbtable.rowClick(result.row, result.column);
      },

      // switch开关
      switchChange(result){
        //  数据库里的值0正常1关闭
        var val = result.e; // 0:关闭状态，1开启状态
        var jobId = result.data.jobId; // 主键ID
        var obj = { jobId : jobId, status : Number(val) };
        if (val == "0" || val == 0){ // 去暂停
          this.$api.job.pause([jobId]).then((res) => {
            if (res.code == 0){
              this.$message({ message: '暂停成功', type: 'success' });
            } else {
              this.$message({ message: '暂停失败', type: 'error' });
            }
          });
        } else if(val == "1" || val == 1) { // 去开启
          this.$api.job.resume([jobId]).then((res) => {
            if (res.code == 0){
              this.$message({ message: '恢复成功', type: 'success' });
            } else {
              this.$message({ message: '恢复失败', type: 'error' });
            }
          });
        }
      },

      selectable:function(row, index){
        console.log(1);
      },

    },
    mounted() {
    }
  }
</script>

<style scoped>

</style>









